import httpTool from '../utils/httpTool'

/** 获取问题类别 */
export const askLb = () => httpTool('/v3/ask/lb');

/** 获取用户信息 */
export const userData = data => httpTool('/v3/ask/user',data);

/** 获取滚动广告 */
export const swipeData = data => httpTool('/v2/adv/news',data);



