import Vue from 'vue'
import Router from 'vue-router'

const home = r => require.ensure([],()=> r (require('../page/home/home')),'home')
const askDetail = r => require.ensure([],()=> r (require('../page/ask/askDetail')),'askDetail')
const publishAsk = r => require.ensure([],()=> r (require('../page/ask/publishAsk')),'publishAsk')
const category = r => require.ensure([],()=> r (require('../page/ask/category')),'category')
const answer = r => require.ensure([],()=> r (require('../page/ask/answer')),'answer')
const expertlist = r => require.ensure([],()=> r (require('../page/home/expertlist')),'expertlist')
const expert = r => require.ensure([],()=> r (require('../page/home/expert')),'expert')

// 用户模块
const myinfo = r => require.ensure([],()=> r (require('../page/myinfo/myinfo')),'myinfo')
const myAsk = r => require.ensure([],()=> r (require('../page/myinfo/myAsk')),'myAsk')
const myAnswer = r => require.ensure([],()=> r (require('../page/myinfo/myAnswer')),'myAnswer')
const myFollow = r => require.ensure([],()=> r (require('../page/myinfo/myFollow')),'myFollow')


const login = r => require.ensure([],()=> r (require('../page/login/login')),'login')
const modifypd = r => require.ensure([],()=> r (require('../page/myinfo/modifypd')),'modifypd')
const register = r => require.ensure([],()=> r (require('../page/myinfo/register')),'register')

const info = r => require.ensure([],()=> r (require('../page/myinfo/info')),'info')

const search = r => require.ensure([],()=> r (require('../page/myinfo/search')),'search')

// 百科
const baike = r => require.ensure([],()=> r (require('../page/home/baike')),'baike')
const baikeDetail = r => require.ensure([],()=> r (require('../page/ask/baikeDetail')),'baikeDetail')

// pc
// 首页
const index = r => require.ensure([],()=> r (require('../page/home/index')),'index')
// 分类
const category_pc = r => require.ensure([],()=> r (require('../page/home/category_pc')),'category_pc')
// 百科
const baike_pc = r => require.ensure([],()=> r (require('../page/home/baike_pc')),'baike_pc')
// 提问
const publishAsk_pc = r => require.ensure([],()=> r (require('../page/home/publishAsk_pc')),'publishAsk_pc')
// 提问详情
const askDetail_pc = r => require.ensure([],()=> r (require('../page/home/askDetail_pc')),'askDetail_pc')
// 我的提问
const myAsk_pc = r => require.ensure([],()=> r (require('../page/home/myAsk_pc')),'myAsk_pc')
// 我的搜索
const search_pc = r => require.ensure([],()=> r (require('../page/home/search_pc')),'search_pc')
// 专家列表
const expertlist_pc = r => require.ensure([],()=> r (require('../page/home/expertlist_pc')),'expertlist_pc')
// 专家内页
const expert_pc = r => require.ensure([],()=> r (require('../page/home/expert_pc')),'expert_pc')
// 百科列表
const baikeList_pc = r => require.ensure([],()=> r (require('../page/home/baikeList_pc')),'baikeList_pc')
// 百科内页
const baikeDetail_pc = r => require.ensure([],()=> r (require('../page/home/baikeDetail_pc')),'baikeDetail_pc')
// 登录
const login_pc = r => require.ensure([],()=> r (require('../page/home/login_pc')),'login_pc')

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    // {
    //   path: '/',
    //   name: 'Hello',
    //   component: Hello
    // },
    { path: '/', name: 'Home', component: home },

    /*****  首页模块  *****/
    // 首页
    { path: '/home', name: 'Home', component: home },

    /*****  问答模块  *****/
    // 问题详情
    { path: '/askDetail', name: 'AskDetail', component: askDetail },

    // 发布问题
    { path: '/publishAsk', name: 'PublishAsk', component: publishAsk },

    // 分类列表
    { path: '/category', name: 'Category', component: category },

    // 回答
    { path: '/answer', name: 'Answer', component: answer },

    // 专家列表
    { path: '/expertlist', name: 'Expertlist', component: expertlist },

    // 专家主页
    { path: '/expert', name: 'Expert', component: expert },

    /*****  百科模块  *****/
    // 百科首页
    { path: '/baike', name: 'Baike', component: baike },

    // 百科详情
    { path: '/baikeDetail', name: 'DaikeDetail', component: baikeDetail },

    /*****  用户模块  *****/
    // 登录
    { path: '/login', name: 'Login', component: login },

    // 个人中心
    { path: '/myinfo', name: 'Myinfo', component: myinfo },

    // 我的提问
    { path: '/myAsk', name: 'MyAsk', component: myAsk },

    // 我的回答
    { path: '/myAnswer', name: 'MyAnswer', component: myAnswer },

    // 我的关注
    { path: '/myFollow', name: 'MyFollow', component: myFollow },

    // 用户修改密码
    { path: '/modifypd', name: 'Modifypd', component: modifypd },

    // 用户注册
    { path: '/register', name: 'Register', component: register },

    // 搜索
    { path:'/search', name: 'Search', component: search },

    /*****  pc模块  *****/
    // 首页
    { path:'/index', name: 'Index', component: index },

    // 分类 publishAsk_pc
    { path:'/category_pc', name: 'Category_pc', component: category_pc },

    // 百科
    { path:'/baike_pc', name: 'Baike_pc', component: baike_pc },

    // 在线提问
    { path:'/publishAsk_pc', name: 'PublishAsk_pc', component: publishAsk_pc },

    // 我的提问
    { path:'/myAsk_pc', name: 'MyAsk_pc', component: myAsk_pc },

    // 提问
    { path:'/askDetail_pc', name: 'AskDetail_pc', component: askDetail_pc },

    // 搜索
    { path:'/search_pc', name: 'Search_pc', component: search_pc },

    // 专家列表
    { path:'/expertlist_pc', name: 'Expertlist_pc', component: expertlist_pc },

    // 专家内页
    { path:'/expert_pc', name: 'Expert_pc', component: expert_pc },

    // 百科列表
    { path:'/baikeList_pc', name: 'BaikeList_pc', component: baikeList_pc },

    // 百科内页
    { path:'/baikeDetail_pc', name: 'BaikeDetail_pc', component: baikeDetail_pc },

    // 登录
    { path:'/login_pc', name: 'Login_pc', component: login_pc },

  ]
})



