import Vue from 'vue'
import Vuex from 'vuex'

import {askLb} from '../api/getData'

Vue.use(Vuex);

const state = {
  askLB:null,
}

const mutations = {
  saveAskLB(state, askLB){
		state.askLB = askLB;
	}
}

const actions={
  async getAskLBData({commit}){
    try{
      console.log('lllll');
      const res = await askLb()
      state.askLB = res;
      // commit('saveAskLB', res);
    }catch(err){
      console.log(err)
    }
  },
  gotoMyinfo({commit}){
    console.log(commit)
    //_vm.$router.push('/myAsk_pc')
  },

};

// 需要导出 Store 对象
export default new Vuex.Store({
  state,
  actions,
  mutations,
});



