<script type="text/javascript">
const colorList = [
  '#F9F900',
  '#6FB7B7',
  '#9999CC',
  '#B766AD',
  '#B87070',
  '#FF8F59',
  '#FFAF60',
  '#FFDC35',
  '#FFFF37',
  '#B7FF4A',
  '#28FF28',
  '#1AFD9C',
  '#00FFFF',
  '#2894FF',
  '#6A6AFF',
  '#BE77FF',
  '#FF77FF',
  '#FF79BC',
  '#FF2D2D',
  '#ADADAD'
]
const colorListLength = 20
const APIHost = 'http://api.07358.com' // api 主机头
const PICHost = 'http://jimg.07358.com/' // 图片主机头

// 获取随机色
function getRandColor () {
  var tem = Math.round(Math.random() * colorListLength)
  return colorList[tem]
}



export default
{
  colorList,
  colorListLength,
  getRandColor,
  APIHost,
  PICHost,
}
</script>



